@import url(https://fonts.googleapis.com/css?family=PT+Sans);
body {
  margin: 0;
  padding: 0;
  font-family: PT Sans, sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.Tooltip {
  position: absolute;
  z-index: 1;

}

.Tooltip__container {
  position: absolute;
  width: 150px;
  padding: 1rem;
  pointer-events: none;
  background: white;
  box-shadow: 3px 3px 5px 0px rgba(176,169,176,1);
}

.Tooltip__line {
  pointer-events: none;
  position: absolute;


  top: 0;
  bottom: 0;
  width: 1px;

  background: rgba(0, 0, 0, 0.28);
}

.Chart {
  position: relative;
}

.Chart__women-area {
  stroke: #c0392b;
  stroke-width: 2;
  fill: rgba(231, 76, 60, 0.4);
}

.Chart__men-area {
  stroke: #2980b9;
  stroke-width: 2;
  fill: rgba(41, 128, 185, 0.4);
}

.Chart__common-area {
  stroke: rgba(44, 62, 80, 1);
  stroke-width: 2;
  fill: none;
}

.Chart__total-year-value {
  text-align: center;
}

.Chart__tick line {
  stroke: #eaf0f6;
  background: white;
}

.Chart__markers {
  overflow: hidden;
  padding-left: 100px;
  padding-right: 100px;
}

.Layout {
}

.Layout__header {
  text-align: center;
}

.Layout__chart-placeholder {
  height: 400px;
}

.Layout__legend-placeholder {
  text-align: center;
  margin-top: 30px;
}
.Legend {
    display: block;
    text-align: center;
}

.Legend__row {
    display: inline-block;
    margin: 0 10px;
}

.Legend__indicator {
    width: 10px;
    height: 10px;
    display: inline-block;
}

.Legend__indicator_common {
    background: black
}

.Legend__indicator_men {
    background: #2980b9;
}

.Legend__indicator_women {
    background: #c0392b;
}

.Legend__indicator_death-rate {
    background: -webkit-linear-gradient(left, #bdc3c7, #2c3e50);
    background: linear-gradient(to right, #bdc3c7, #2c3e50);
    width: 100px;
}
